body,
html {
  height: 100%;
}

.jumbotron-home {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("./media/homepage-hero.jpg");
  height: 30vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.homepage-img {
  width: 100%;
  height: 15rem;
}

