.fa {
    padding: 1rem;
    font-size: 1.6rem;
    width: 3rem;
    text-align: center;
    text-decoration: none;
    margin: 1rem 0.1rem;
    border-radius: 50%;
  }
  
  .fa:hover {
      opacity: 0.7;
  }
  
  .fa-facebook {
    background: #3B5998;
    color: white;
  }
  
  .fa-twitter {
    background: #55ACEE;
    color: white;
  }
  
  .fa-google {
    background: #dd4b39;
    color: white;
  }
  
  .fa-youtube {
    background: #bb0000;
    color: white;
  }
  
  .fa-instagram {
    background: #125688;
    color: white;
  }

  .fa-phone {
    background: #16532d;
    color: white;
  }
  
  
  