body,
html {
  height: 100%;
}
.aboutpage {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("./media/about_page.jpg");
  height: 83vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.aboutpage-new {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("./media/about_page.jpg");
  min-height: 83vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.text-wrapper-div {
  background-color: white;
  opacity: 1;
  text-align: center;
  margin-top: 5rem;
  margin-bottom: 5rem;
  background-color: rgba(240, 240, 240, 1);
}

.text-inner-wrapper-div {
  margin: 1rem;
}


