.aboutpage-heroimage {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("./media/about_page.jpg");
  min-height: 15rem;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.aboutpage-text-wrapper-inner {
  text-align: center;
  margin-top: 5rem;
  margin-bottom: 5rem;
}

@media only screen and (min-width: 960px) {
  .aboutpage-text-wrapper-inner {
    text-align: center;
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
}

@media only screen and (max-width: 960px) {
  .aboutpage-text-wrapper-inner {
    text-align: center;
    margin-top: 3rem;
    margin-bottom: 5rem;
  }
}