body,
html {
  height: 100%;
}
.bookpage {
  background-color: rgba(220, 220, 220, 0.8);
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.papa_book {
  margin: 0;
  position: absolute;
  background-color: rgba(20, 20, 20, 0.8);
  opacity: 1;
  top: 30%;
  left: 30%;
  right: 30%;
  bottom: 45%;
}

.texto_book {
  background-color: none;
  opacity: 1;
  color: white;
}

.card1 {
  right: "15%";
  top: "0%";
  bottom: "0%";
  margin: "50px 50px 0px 50px";
}

.bookpage-heroimage {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), url("./media/bookpage-hero.jpg");
  min-height: 15rem;
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
}

.bookpage-header-title {
  color:white;
  text-align: center;
  padding-top: 5rem;
}

@media only screen and (min-width: 960px) {
  .container-booking {
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
}

@media only screen and (max-width: 960px) {
  .container-booking {
    margin-top: 3rem;
    margin-bottom: 5rem;
  }
}

.margin-topper {
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.coolrow {
  margin-bottom: 3rem;
}

.custom-img {
  height: 15rem;
}

.pic-test {
  background-image: url("https://source.unsplash.com/1600x900/?nature,water");
  height: 30vh;
}